/* Google Fonts
--------------------------------------------------------------------------------*/
@import url(https://fonts.googleapis.com/css?family=Manrope);

/* Default Element Styles
--------------------------------------------------------------------------------*/
body {
    margin: 0;
    background-color: #F5F5F5; 
    font-family: 'Manrope';
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
     monospace;
}

ul {
    list-style: none;
    padding: 0;
}

a {
    color: #A9A9A9;
    text-decoration: none;
}

a:hover {
    cursor: pointer;
    color: black;
    text-decoration: none;
}

a:active {
    color: black;
    text-decoration: none;
}

/* Sticky Footer
--------------------------------------------------------------------------------*/
html, body {
    height: 100%;
}

.wrapper {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}

.main {
    flex: 1;
}

/* Layout
--------------------------------------------------------------------------------*/
.wrapper {
    text-align: center;
    padding: 0;
    max-width: 785px;
    margin: 0 auto;
}

.active {
    color: black;
    text-decoration: none;
}

.my-name {
    font-size: 28px;
    margin-top: 30px;
}

.bio {
    color: #A9A9A9;
    font-size: 16px;
    margin: 10px 0;
}

.nav ul {
    margin: 0 auto;
}

.nav ul li {
    display: inline;
    padding: 0 20px;
}

.content {
    margin: 10px 0;
}

.social-media li {
    display: inline;
    padding: 20px;
    font-size: 25px;
}

.gallery img {
    width: 100%;
    padding: 10px;
}

.about {
    text-align: left;
}

.about p {
    text-indent: 40px;
}

.about-hero {
    width: 100%;
    margin-bottom: 20px;
}

.footer {
    margin-top: 20px;
}

/* Breakpoint Styles
--------------------------------------------------------------------------------*/
@media only screen and (max-width: 600px) {
    .wrapper {
        max-width: 100%;
    }

    .about p {
        max-width: 350px;
        margin: 0 auto;
    }
    
    .gallery p {
        font-size: 12px;
    }
}
